@import "codemirror/lib/codemirror.css";
@import "codemirror/theme/material.css";

.Json2Types {
  display: grid;
  grid-template-columns: repeat(2, 1fr);

  background-color: #263238;
}

.Json2Types .left,
.Json2Types .right {
  overflow-y: auto;
}

.Json2Types .left {
  border-right: 1px solid white;
}

.CodeMirror {
  border: 1px solid #eee;
  height: 500px;
  border: none;
}

.CodeMirror-scroll {
  height: 100%;
  overflow-y: hidden;
  overflow-x: auto;
}
